// Project content styles

.project-content {
  margin-top: 6*$baseline;
  margin-bottom: 3*$baseline;
  @extend .content--fadeIn;

  h1 {
    text-align: center; }

  // a smaller version of h2
  h2 {
    @include typeScale(3);
    margin-bottom: 0.5 * $baseline; }

  h4 {
    @include typeScale(4);
    text-align: center;
    width: 100%;
    max-width: 24rem;
    margin: 0 auto;
    margin-bottom: $baseline;
    font-weight: $fontWeight-primary-regular;
    color: $color-grey-3; }

  // markdown styling
  ul {
    margin-left: $baseline; }

  img {
    margin: $baseline 0; } }

// Fluidbox enabled image gallery
.project-gallery {
  width: 100%;
  @extend .clearfix;

  // image with caption
  img {
    margin-bottom: 0.5*$baseline; }

  .image-caption {
    color: $color-grey-3;
    display: block;
    text-align: center;
    margin-bottom: $baseline; } }

.project-grid-2 {
  width: 100%;
  display: inline-block;

  @media #{$medium-up} {
    width: 50%;
    float: left;

    &:nth-child(2n+1) {
      padding-right: 0.5*$baseline; }

    &:nth-child(2n) {
      padding-left: 0.5*$baseline; } } }


